body {
  margin: 0;
  padding: 0;
  font-family: "Inter", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7FAFF;
  color: #2E353F;

}

.todo-item-container {
  background-color: white;
  color: #3A3131;
  padding: 1.5em 0.8em;
  display:inline-block;
  justify-content: space-between;
  min-height: 27px;
}

.todo-item-container-left {
  display: flex;
}

.todo-item-complete-button {
  border: 0;
  background: none;
  cursor: pointer;
  margin-right: 2px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-complete-button:focus {
  outline: 0;
}

.todo-item-circle {
  border-radius: 100%;
  width: 24px;
  height: 24px;
}

.todo-item-circle-empty {
  box-sizing: border-box;
  border: 2px solid #A2ADC1;
}

.todo-item-circle-empty:hover {
  background: #a2adc12e;
}

.todo-item-circle-check {
  background: #47D80C;
  color: white;

  /*
    center children vertically and horizontally
    https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
}

.todo-item-circle-check > svg {
  width: 15px;
  height: 15px;
}

.todo-item-description {
  margin-bottom: 8px;
}

.todo-item-start-session-button {
  color: #A2ADC1;
  border: 0;
  background: none;
  cursor: pointer;
  display: none;
}

.todo-item-start-session-button > svg {
  width: 24px;
  height: 24px;
}

.todo-item-start-session-button:hover > svg {
  width: 25px;
  height: 25px;
}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.todo-item-start-session-button:focus {
  outline: 0;
}

.todo-item-container:hover .todo-item-start-session-button {
  display: block;
}

.sessions-completed-container {
  display:flex;
  flex-direction:row;
}

.sessions-completed-line {
  background: #D6D6D6;
  width: 14px;
  height: 3px;
  border-radius: 1px;
  margin-left: 3px;
}

.todo-input-container > form {
  display: flex;
}

.todo-input, .todo-input-button, h5 {
  box-sizing: border-box;
  padding-top: 0.7em;
  padding-bottom: 0.7em;
  font-size: 1.2em;
  font-weight: 600;
  border: 0;
}

.todo-input {
  padding-left: 1em;
  background: #E7EDF8;
  border-radius: 4px 0 0 4px;
  flex-grow: 1;
}
.todo-input::-webkit-input-placeholder {
  
  color: #8A98AF;
}
.todo-input::-moz-placeholder {
  
  color: #8A98AF;
}
.todo-input::-ms-input-placeholder {
  
  color: #8A98AF;
}
.todo-input::placeholder {
  
  color: #8A98AF;
}

.todo-input.missing {
  box-shadow: inset 0 0 0 2px var(--red);
}

.todo-input-button {
  background: #4A6ED2;
  color: white;
  border-radius: 55px 55px 55px 55px;/*55px 55px 55px 55px;*/
  /*
  center children vertically and horizontally
  https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  /* minimum size */
  flex-basis: 2.7em;
  cursor: pointer;
}

.todo-input-button > svg {
  width: 1em;
  height: 1em;
}
h5 {
  padding-left: 1em;
  background: #E7EDF8;
  border-radius: 4px 0 0 4px;
  flex-grow: 1;
  color: #8A98AF;
  width: 250px;
  display: inline-flexbox;
}
.row {
  display: block;
}

.time-input {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  height: 30px;
  width: 50%;
  max-width: 150px;
  border-radius: 30px;
  border: 1px solid lightgray;
  padding: 2px 20px;
  font-size: 10pt;
}

.time-input:focus {
  outline: none;
  border: 2px solid #1dcaff;
  background-color: white;
  -webkit-transition: 0.2s ease all;
  transition: 0.2s ease all;
}
img {
  width: 300px;
  height: 200px;
}

.right {
  display: inline;
  width: 50%;
  height: 100%;
  float: left;
}
.left {
  display: block;
  width: 50%;
  height: 100%;
  float: right;
}


.flex-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 1.1em;
}
.innerContainer {
  display: flex;
}
.nav {
  padding-left: 30px;
  min-width: 50%;
  outline: 5px dotted #cce6ff;
}
.middle {
  
  min-width: 3%;
}

.main {
  max-width: 50%;
  min-width: 140px;
  flex: 1 1;
  
}

header {
  margin-bottom: 0.9em;
  display: flex;
}

.heading {
  font-size: 2.6em;
  font-weight: 800;
  flex-grow: 70;
}

.clear-button {
  color: grey;
  border: 0;
  background: none;
  cursor: pointer;
  margin-right: 0.2em;
  /*
  center children vertically and horizontally
  https://stackoverflow.com/questions/25832340/css-flexbox-vertically-horizontally-center-image-without-explicitely-defining-pa
  */
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */

}

/* bad for a11y but looks better ðŸ¤·â€ */
/* good fix: https://medium.freecodecamp.org/how-to-make-your-fancy-svg-button-accessible-83c9172c3c15 */
.clear-button:focus {
  outline: 0;
}

.clear-button:hover > svg {
  width: 31px;
  height: 31px;
}

.clear-button > svg {
  width: 30px;
  height: 30px;
}

.card {
  border-radius: 9px;
  box-shadow: rgba(0, 0, 0, 0.11) 0px 4px 8px;
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
  margin-bottom: 1em;
}

.card:hover {
  box-shadow: rgba(0, 0, 0, 0.13) 0px 6px 20px;
}

.empty-state-container {
  display: flex;
  flex-direction: column;
}

.empty-state-container > svg {
  margin: auto;
  flex-basis: 20em;
}

.empty-state-text {
  text-align: center;
  font-size: 1.2em;
}
h1 {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #8A98AF;
}
